<template>
	<div class="muen">
		<div class="topImg">
			<img class="auto-img" src="../assets/images/mytopimg.jpg" alt="">
		</div>
		<!-- 顶部切换菜单类型 -->
		<van-tabbar v-model="active" style="top: 0;" active-color="#52DE97" inactive-color="#666">
			<van-tabbar-item v-for="(item,index) in pathIcon" :key="index" @click="getDifferentCom(item.ename)">
				<span>{{item.name}}</span>
				<template #icon="props">
					<img :src="props.active ? item.active : item.inactive" />
				</template>
			</van-tabbar-item>
		</van-tabbar>
		<!-- 类别切换 -->
		<div class="typeList">
			<ul>
				<li v-for="(item,index) in recomData" :key="index" @click="goProduct(item.pid)">
					<div class="comImg">
						<img class="auto-img" :src="item.largeImg" alt="">
					</div>
					<div class="comName">
						<div class="chName">{{item.name}}</div>
						<div class="egName">{{item.enname}}</div>
					</div>
					<div class="price">￥{{item.price}}</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import "../assets/less/Muen.less"
	export default {
		name: "Muen",
		data() {
			return {
				active: 0,
				//定义切换选择
				pathIcon: [{
						name: "推荐",
						active: require('../assets/images/from01.png'),
						inactive: require('../assets/images/from01-1.png'),
						ename: "1"
					},
					{
						name: "拿铁",
						active: require('../assets/images/from03.png'),
						inactive: require('../assets/images/from03-1.png'),
						ename: "latte"
					},
					{
						name: "咖啡",
						active: require('../assets/images/from02.png'),
						inactive: require('../assets/images/from02-1.png'),
						ename: "coffee"
					},
					{
						name: "瑞纳冰",
						active: require('../assets/images/from04.png'),
						inactive: require('../assets/images/from04-1.png'),
						ename: "rena_ice"
					},

				],
				//获取推荐商品
				recomData: [],
				recomData1: [],
				//获取商品类型
				recomType: [],
				//拿铁类型
				latte: '',
				//咖啡类型
				coffee: '',
				//瑞纳冰
				renaIce: '',

			}
		},
		created() {
			this.getRecommend()
			this.getType();
		},
		methods: {
			//跳转详情页
			goProduct(pid) {
				this.$router.push({name: "ProductDetails",params: {pid}});
			},
			//获取商品类型
			getType() {
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 50
				});
				this.axios({
					method: 'GET',
					url: '/type',

					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 400) {
						this.recomType = res.data.result
						// console.log(this.recomType );
						this.latte = this.recomType[0].type
						this.coffee = this.recomType[1].type
						this.renaIce = this.recomType[2].type
					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 50
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},

			//获取推荐商品
			getRecommend() {
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 50
				});
				this.axios({
					method: 'GET',
					url: '/typeProducts',
					params: {
						key: 'isHot',
						value: 1
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 500) {
						this.recomData = res.data.result
						this.recomData1 = res.data.result
						// console.log(this.recomData );
					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 50
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
			//获取不同类型的商品
			getDifferentCom(types) {
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 50
				});
				if (types == 1) {
					this.recomData = this.recomData1;
					return
				}
				this.axios({
					method: 'GET',
					url: '/typeProducts',
					params: {
						key: 'type',
						value: types
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 500) {
						this.recomData = res.data.result
						// console.log(this.recomData );
					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 50
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			}
		},


	}
</script>

<style>
